<template>
  <view>
	  
	  <my-search  @myclick="gotoSearch" :bgcolor= "'#c00000'"   ></my-search>
    <view class="scroll-view-container">
      <!-- 左侧的滚动视图区域 -->
      <scroll-view class="left-scroll-view" scroll-y :style="{height: wh + 'px'}">
    
      
          <block v-for="(item, i) in cateList" :key="i">
            <view  :class="[i===active?'active':'']"  @click="activeChange(i)" class="left-scroll-view-item">{{item.cat_name}}</view>
          </block>

      </scroll-view>
  <!-- 右侧的滚动视图区域 -->
  <scroll-view  :scroll-top="scrollTop" class="right-scroll-view" scroll-y :style="{height: wh + 'px'}">
    <view class="cate-lv2" v-for="(item2, i2) in cateLevel2" :key="i2">
      <view class="cate-lv2-title">/ {{item2.cat_name}} /</view>
      <!-- 动态渲染三级分类的列表数据 -->
      <view class="cate-lv3-list">
       <!-- 三级分类 Item 项 -->
        <view class="cate-lv3-item" v-for="(item3, i3) in item2.children" :key="i3" @click="gotoGoodsList(item3)">
        <!--  图片 -->
        <image :src="item3.cat_icon.replace('dev','web')"></image>
          <!-- 文本 -->
          <text>{{item3.cat_name}}</text>
        </view>
      </view>
    </view>
  </scroll-view>
    </view>
  </view>
</template>

<script>
	export default {
		data() {
			return {
				wh:0,
				cateList:[],
				  // 当前选中项的索引，默认让第一项被选中
			active: 0,
			 // 二级分类列表
			    cateLevel2: [],
				scrollTop:0,
			};
		}
		,onLoad(){
   // 获取当前系统的信息
      const sysInfo = uni.getSystemInfoSync()
      // 为 wh 窗口可用高度动态赋值
 // 可用高度 = 屏幕高度 - navigationBar高度 - tabBar高度 - 自定义的search组件高度
   this.wh = sysInfo.windowHeight - 50
	   // 调用获取分类列表数据的方法
	    this.getCateList()
		},
		methods: {
		 
		  async getCateList() {
		    // 发起请求
		    const { data: res } = await uni.$http.get('/api/public/v1/categories')
		    // 判断是否获取失败
		    if (res.meta.status !== 200) return uni.$showMsg()
			
			console.log(res.message)
			// console.log(res)
		    // 转存数据
		     this.cateList = res.message
			 // 为二级分类赋值
			 
			  this.cateLevel2 = res.message[0].children
		  },
		  activeChange(i){
			  this.active=i
			  
			   // 为二级分类列表重新赋值
			   this.cateLevel2 = this.cateList[i].children
			    // 让 scrollTop 的值在 0 与 1 之间切换
			     this.scrollTop = this.scrollTop === 0 ? 1 : 0
		  },
		  // 点击三级分类项跳转到商品列表页面
		  gotoGoodsList(item3) {
		    uni.navigateTo({
		      url: '/subpkg/goods_list/goods_list?cid=' + item3.cat_id
		    })
		  },
		  gotoSearch(){
			  console.log(11111)
	uni.navigateTo({
		url:'/subpkg/search/search'
	})
		  }
		  
		}
	}
</script>

<style lang="scss">
.scroll-view-container{
	display: flex;
	.left-scroll-view{
		width: 120px;
		.left-scroll-view-item{
		background-color: #f7f7f7;
		text-align: center;
		line-height: 60px;
		font-size: 12px;
		&.active{
			background-color: #FFFFFF;
			
				position: relative;
			&::before{
				content: '';
				display: block;
				width: 3px;
				height: 30px;
				background-color: #c00000;
			   position: absolute;
			   top: 50%;
			   left: 0;
			   transform: translateY(-50%);
			}
		}
	}
	}
	.right-scroll-view{
	.cate-lv2-title {
	  font-size: 12px;
	  font-weight: bold;
	  text-align: center;
	  padding: 15px 0;

	}
	.cate-lv3-list{
			   display: flex;
				  flex-wrap: wrap;
			  .cate-lv3-item{
				 width: 33.33%;
			 display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			margin-bottom: 10px;
				  image{
					 width: 60px;
					  height: 60px;
				  }
				  text{
					  font-size: 12px;
				  }
			  }
			  
	}
	}


}

</style>
